/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"fromrachel.com\">From Rachel</a> is an e-commerce business allowing women to shop and subscribe to monthly boxes of fashionable nylon thights. With more than 60 models, each subscriber gets a personalized selection of tights in each of their boxes based on their preferences. The project aimed to encourage customers to communicate with 'Rachel' on a frequent basis, starting with their style preferences, an essential information to select the products they will receive.</p>\n<h1 class=\"w-100 text-center my-7\">Survey's might get the job done but they do very little at building brand experience, foster communication and encourage people to reengage.</h1>\n<h2>Context</h2>\n<p>Since many subscribers had developed the habit of talking to the company by writing directly to 'Rachel', I figured we had to opportunity to encourage it. A questionnaire can hardly by itself promote communication or provide enough value for customers to willingly come back and fill it over and over again.</p>\n<p>Based on earlier research by the team, a clear profile of 'Rachel' had already been developped, and we knew, subscribers loved her for her advice and personality. If we managed to create a more personal interaction with the character, would they be interested enough to seek to opportunity to interact with her on a regular basis?</p>\n<p>That's what I set out to try, and the style profile is the first experiment to figure out which kind of interaction with 'Rachel' genuinely provides value for both the customer and the company.</p>\n<h2>Design</h2>\n<p>The focus was put on 2 specific aspects:</p>\n<ul>\n<li>Implement a simple, intuitive and 'fun' input method to interact with Rachel.</li>\n<li>Balance the focus between Rachel (the conversation) and the visual content.</li>\n</ul>\n<p>That balance was especially important to strike because after all, 'Rachel' is not the value, it's the tights. Fostering a sense of excitement over their next box is the underlying objective.</p>\n<p>In contrast with other conversational UI, talking with Rachel is primarily turned into a visual experience by making use of large visuals that are used as the input method. By making of use of sidescrolling to browse the choices, the visuals can remain large, but also maintain the context of the conversation. The input section is also contained within a different background color to shift the focus away from the conversation toward the visual content.</p>\n<div class=\"d-flex justify-content-center\">\n  <img src=\"/input-edc582b8006206b87f34170a84ed8cb2.gif\">\n</div>\n<h2>Results</h2>\n<p>To try the style profile, you will have to create and account at <a href=\"fromrachel.com/my-account\">fromrachel.com/my-account</a> and click on the 'style profile' button in your account or in the welcome email. Here is a gif of the interactions with Rachel's.</p>\n<div class=\"d-flex justify-content-center bg-light\">\n  <img src=\"/rachel-animated-7f2d19531be03dfbeae9cd30bad3748e.gif\">\n</div>","frontmatter":{"date":"2017-10-01T12:00:00Z","path":"/rachel-style-profile","title":"Rachel's style profile","timeframe":"Sept - Oct 2017","images":{"first":"rachel-1","second":"rachel-2"},"gradient":{"left":"#f7bbb5","right":"#f9ecde"}}}},"pathContext":{}}

/***/ })

